import { LargeTitle, Text } from "@telegram-apps/telegram-ui";
import { request } from "@tma.js/sdk";
import { useMainButton } from "@tma.js/sdk-react";
import { useEffect, useState } from "react";

export function Time() {
  const [time, setTime] = useState(new Date());

  useEffect(() => {
    const timer = setInterval(() => {
      setTime(new Date());
    }, 300);
    return () => clearInterval(timer);
  }, []);

  const mainButton = useMainButton();

  useEffect(() => {
    if (!mainButton) {
      return;
    }
    mainButton.setText("Send current time to chat");
    const onClickRemover = mainButton.on("click", () =>
      request({
        method: "web_app_data_send",
        event: "main_button_pressed",
        params: {
          data: "coucou",
        },
      })
    );
    mainButton.show();
    return onClickRemover;
  }, [mainButton]);

  return (
    <div>
      <LargeTitle weight={"1"}>Time is</LargeTitle>
      <Text>{time.toLocaleString()}</Text>
    </div>
  );
}
